/* VLR.gg Inspired Global Styles - PRODUCTION READY */
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Root CSS Variables for VLR.gg style theming */
:root {
  --color-primary: #dc2626; /* Red-600 */
  --color-primary-hover: #b91c1c; /* Red-700 */
  --color-secondary: #6b7280; /* Gray-500 */
  --color-accent: #3b82f6; /* Blue-500 */
  --color-success: #10b981; /* Green-500 */
  --color-warning: #f59e0b; /* Yellow-500 */
  --color-danger: #ef4444; /* Red-500 */
}

/* FIXED: Dark theme with proper VLR.gg colors - 15% brighter blue background */
.dark {
  --bg-primary: #2a2a35; /* VLR.gg dark background - 15% brighter */
  --bg-secondary: #1f1f27; /* VLR.gg darker background - 15% brighter */
  --bg-card: #333340; /* VLR.gg card background - 15% brighter */
  --text-primary: #ffffff; /* Pure white text */
  --text-secondary: #a3a3a3; /* Light gray text */
  --border-color: #444450; /* Dark border - 15% brighter */
}

/* FIXED: Light theme with proper VLR.gg style */
.light {
  --bg-primary: #f3f4f6; /* VLR.gg light gray background - NO bright white */
  --bg-secondary: #ffffff;
  --bg-card: #ffffff; /* Card backgrounds can be white in light mode */
  --text-primary: #111827; /* Dark text */
  --text-secondary: #6b7280; /* Gray text */
  --border-color: #e5e7eb; /* Light border */
}

/* Base styles - FIXED */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

/* FIXED: VLR.gg Style Global Components - NO WHITE BOXES */
.card {
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  @apply rounded-lg shadow-sm transition-all duration-200 hover:shadow-md;
}

.btn {
  @apply px-4 py-2 rounded-md font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 cursor-pointer inline-flex items-center justify-center;
}

.btn-primary {
  @apply bg-red-600 text-white hover:bg-red-700 focus:ring-red-500 disabled:opacity-50 disabled:cursor-not-allowed hover:shadow-lg hover:shadow-red-500/25;
}

.btn-secondary {
  @apply bg-gray-200 text-gray-900 hover:bg-gray-300 focus:ring-gray-500 dark:bg-gray-700 dark:text-gray-100 dark:hover:bg-gray-600 border border-gray-300 dark:border-gray-600;
}

.btn-ghost {
  @apply bg-transparent text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 border border-transparent;
}

.form-input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: 0.375rem;
  background-color: var(--bg-card);
  color: var(--text-primary);
  transition: all 0.2s ease;
}

.form-input::placeholder {
  color: var(--text-secondary);
}

.form-input:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

/* VLR.gg Style Navigation - PERFECTED */
.nav-item {
  @apply px-3 py-2 text-sm font-medium transition-all duration-200 rounded-md relative;
}

.nav-item.active {
  @apply text-red-600 dark:text-red-400 bg-red-50 dark:bg-red-900/20;
}

.nav-item.active::after {
  content: '';
  @apply absolute bottom-0 left-1/2 transform -translate-x-1/2 w-6 h-0.5 bg-red-600 dark:bg-red-400;
}

.nav-item:not(.active) {
  @apply text-gray-700 dark:text-gray-300 hover:text-red-600 dark:hover:text-red-400 hover:bg-gray-50 dark:hover:bg-gray-700/50;
}

/* VLR.gg Style Forum Components - PERFECTED */
.forum-thread {
  @apply border-b border-gray-200 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-all duration-200 cursor-pointer;
}

.forum-thread:last-child {
  @apply border-b-0;
}

.forum-thread:hover {
  @apply transform translate-x-1;
}

.recent-discussion-item {
  @apply p-3 hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-all duration-200 border-l-2 border-transparent hover:border-red-400;
}

.recent-discussion-title {
  @apply text-sm font-medium text-gray-900 dark:text-white line-clamp-2 mb-1 hover:text-red-600 dark:hover:text-red-400 transition-colors;
}

.recent-discussion-meta {
  @apply text-xs text-gray-500 dark:text-gray-500 flex items-center justify-between;
}

/* FIXED: VLR.gg Style Match Cards - NO WHITE BACKGROUNDS */
.match-card {
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  @apply rounded-lg p-4 transition-all duration-200 hover:shadow-md;
}

.match-card:hover {
  border-color: var(--color-primary);
  opacity: 0.9;
}

.team-logo {
  @apply w-8 h-8 rounded flex items-center justify-center bg-gray-100 dark:bg-gray-700 text-lg;
}

.team-name {
  @apply font-medium text-gray-900 dark:text-white transition-colors duration-200;
}

.team-name:hover {
  @apply text-red-600 dark:text-red-400;
}

/* Status badges - PERFECTED */
.status-live {
  @apply px-2 py-1 bg-red-500 text-white text-xs font-bold rounded-full animate-pulse shadow-lg;
}

.status-upcoming {
  @apply px-2 py-1 bg-blue-500 text-white text-xs font-bold rounded-full;
}

.status-completed {
  @apply px-2 py-1 bg-green-500 text-white text-xs font-bold rounded-full;
}

/* Loading animations - REMOVED */
/* .loading-spinner {
  @apply w-6 h-6 border-2 border-red-300 border-t-red-600 rounded-full animate-spin;
} */

/* Responsive design utilities - PERFECTED */
.responsive-grid {
  @apply grid gap-4;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

/* VLR.gg style compact tables - PERFECTED */
.compact-table {
  @apply w-full text-sm;
}

.compact-table th {
  @apply px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider bg-gray-50 dark:bg-gray-700/50;
}

.compact-table td {
  @apply px-3 py-2 whitespace-nowrap text-gray-900 dark:text-white border-b border-gray-200 dark:border-gray-700;
}

.compact-table tr:hover {
  @apply bg-gray-50 dark:bg-gray-700/50;
}

/* Event tier badges - PERFECTED */
.tier-s {
  @apply bg-gradient-to-r from-yellow-400 to-yellow-500 text-yellow-900 border-yellow-300 shadow-lg shadow-yellow-500/25;
}

.tier-a {
  @apply bg-gradient-to-r from-red-400 to-red-500 text-red-900 border-red-300 shadow-lg shadow-red-500/25;
}

.tier-b {
  @apply bg-gradient-to-r from-blue-400 to-blue-500 text-blue-900 border-blue-300 shadow-lg shadow-blue-500/25;
}

.tier-c {
  @apply bg-gradient-to-r from-green-400 to-green-500 text-green-900 border-green-300 shadow-lg shadow-green-500/25;
}

/* Animations - PERFECTED */
.animate-fade-in {
  animation: fadeIn 0.4s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-slide-up {
  animation: slideUp 0.4s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-scale-in {
  animation: scaleIn 0.3s ease-out;
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-slide-up {
  animation: slideUpMobile 0.3s ease-out;
}

@keyframes slideUpMobile {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Glass effect for search pages - PERFECTED */
.glass {
  @apply backdrop-blur-md bg-white/90 dark:bg-gray-800/90 border border-gray-200/50 dark:border-gray-600/50 shadow-xl;
}

/* Bracket visualization mobile styles */
.bracket-container {
  @apply relative;
}

.bracket-container::-webkit-scrollbar {
  height: 8px;
}

.bracket-container::-webkit-scrollbar-track {
  @apply bg-gray-100 dark:bg-gray-800 rounded;
}

.bracket-container::-webkit-scrollbar-thumb {
  @apply bg-gray-400 dark:bg-gray-600 rounded hover:bg-gray-500;
}

/* Mobile bracket specific */
@media (max-width: 980px) {
  .bracket-container {
    @apply -mx-4 px-4;
  }
  
  .bracket-round {
    min-width: 200px !important;
  }
  
  .bracket-match {
    @apply text-xs;
    min-width: 180px !important;
  }
  
  .bracket-match .team-logo {
    @apply w-5 h-5;
  }
  
  .bracket-match .team-name {
    @apply text-xs;
  }
  
  .bracket-match input {
    @apply w-10 text-xs;
  }
  
  /* Touch-friendly tap targets */
  .bracket-match {
    @apply min-h-[44px];
  }
  
  /* Mobile bracket header */
  .bracket-header {
    @apply sticky top-0 z-10 bg-white dark:bg-gray-900 pb-2;
  }
  
  /* Pinch to zoom indicator */
  .bracket-zoom-indicator {
    @apply fixed bottom-4 right-4 bg-black/75 text-white px-3 py-2 rounded-lg text-xs flex items-center space-x-2;
  }
}

/* Mobile-optimized tables with scroll indicators */
.mobile-table-container {
  @apply -mx-4 px-4 overflow-x-auto relative;
}

.mobile-table-container::-webkit-scrollbar {
  height: 6px;
}

.mobile-table-container::-webkit-scrollbar-track {
  @apply bg-gray-100 dark:bg-gray-800;
}

.mobile-table-container::-webkit-scrollbar-thumb {
  @apply bg-gray-400 dark:bg-gray-600 rounded;
}

/* Scroll shadow indicators */
.mobile-table-container::before,
.mobile-table-container::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20px;
  pointer-events: none;
  z-index: 10;
}

.mobile-table-container::before {
  left: 0;
  background: linear-gradient(to right, rgba(255,255,255,1), rgba(255,255,255,0));
}

.mobile-table-container::after {
  right: 0;
  background: linear-gradient(to left, rgba(255,255,255,1), rgba(255,255,255,0));
}

.dark .mobile-table-container::before {
  background: linear-gradient(to right, rgba(17,24,39,1), rgba(17,24,39,0));
}

.dark .mobile-table-container::after {
  background: linear-gradient(to left, rgba(17,24,39,1), rgba(17,24,39,0));
}

@media (max-width: 980px) {
  /* Responsive table utilities */
  .table-mobile {
    @apply text-xs;
  }
  
  .table-mobile th,
  .table-mobile td {
    @apply px-2 py-2;
  }
  
  /* Hide less important columns on mobile */
  .hide-mobile {
    @apply hidden;
  }
  
  .show-desktop {
    @apply hidden sm:table-cell;
  }
}

/* Line clamp utilities - PERFECTED */
.line-clamp-1 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.line-clamp-3 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}

/* Touch gesture support */
@media (hover: none) and (pointer: coarse) {
  /* Larger tap targets for touch devices */
  button, a, .clickable {
    min-height: 44px;
    min-width: 44px;
  }
  
  /* Remove hover effects on touch devices */
  .hover\:bg-gray-50:hover,
  .hover\:bg-gray-100:hover,
  .hover\:bg-gray-200:hover,
  .hover\:bg-red-50:hover {
    background-color: transparent;
  }
  
  /* Active states for touch feedback */
  button:active, a:active, .clickable:active {
    @apply transform scale-95 transition-transform duration-100;
  }
}

/* Smooth scrolling for mobile */
@supports (-webkit-overflow-scrolling: touch) {
  .overflow-x-auto,
  .overflow-y-auto,
  .overflow-auto {
    -webkit-overflow-scrolling: touch;
  }
}

/* Prevent text selection on UI elements */
.no-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Mobile responsive improvements - ENHANCED VLR.gg Style */
@media (max-width: 640px) {
  .card {
    @apply rounded-lg shadow-sm mx-0 border-0;
  }
  
  .forum-thread {
    @apply p-3;
  }
  
  .match-card {
    @apply p-3;
  }
  
  .btn {
    @apply px-3 py-1.5 text-sm min-h-[44px];
  }

  .recent-discussion-item {
    @apply p-3 min-h-[44px];
  }
  
  /* Mobile-specific match display */
  .match-team-name {
    @apply text-sm font-medium;
  }
  
  /* Mobile score display */
  .match-score-display {
    @apply text-xl font-bold;
  }
  
  /* Reduce heading sizes on mobile */
  h1 { @apply text-xl sm:text-2xl font-bold; }
  h2 { @apply text-lg sm:text-xl font-semibold; }
  h3 { @apply text-base sm:text-lg font-medium; }
  
  /* Mobile container padding */
  .container {
    @apply px-3;
  }
  
  /* Mobile navigation spacing */
  .nav-item {
    @apply px-3 py-2 text-sm min-h-[44px];
  }
  
  /* Mobile tables - horizontal scroll */
  .table-container {
    @apply overflow-x-auto -mx-3 px-3;
  }
  
  table {
    @apply min-w-full;
  }
  
  /* Mobile cards spacing */
  .space-y-4 > * + * {
    @apply mt-3;
  }
  
  /* Touch targets minimum 44px */
  button, a, .clickable {
    @apply min-h-[44px] flex items-center justify-center;
  }
  
  /* Mobile inputs */
  input, select, textarea {
    @apply min-h-[44px] text-base;
  }
}

/* Tablet responsive improvements - ENHANCED */
@media (min-width: 641px) and (max-width: 1024px) {
  .responsive-grid {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  }
  
  .card {
    @apply mx-0;
  }
  
  /* Tablet-specific match display */
  .match-team-name {
    @apply text-sm max-w-[120px];
  }
  
  /* Tablet container padding */
  .container {
    @apply px-4;
  }
}

/* Small tablet breakpoint (768px) */
@media (min-width: 768px) and (max-width: 1023px) {
  /* Better grid layouts for small tablets */
  .home-layout {
    @apply gap-3;
  }
  
  /* Match cards on tablets */
  .match-card {
    @apply p-3;
  }
}

/* Focus states for accessibility - PERFECTED */
.focus-visible:focus {
  @apply outline-none ring-2 ring-red-500 ring-offset-2 ring-offset-white dark:ring-offset-gray-800;
}

/* Hover effects - PERFECTED */
.hover-lift:hover {
  @apply transform -translate-y-1 shadow-lg transition-all duration-200;
}

.hover-glow:hover {
  @apply shadow-lg shadow-red-500/25 transition-shadow duration-200;
}

.hover-scale:hover {
  @apply transform scale-105 transition-transform duration-200;
}

/* VLR.gg style sidebar improvements - PERFECTED */
.sidebar-compact {
  @apply text-xs space-y-1;
}

.sidebar-compact .sidebar-item {
  @apply p-2 rounded hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors border-l-2 border-transparent hover:border-red-400;
}

/* Admin panel specific styles - PERFECTED */
.admin-dashboard {
  @apply space-y-6;
}

.admin-card {
  @apply bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-6 hover:shadow-md transition-shadow;
}

.admin-stat-card {
  @apply bg-gradient-to-r from-red-500 to-red-600 text-white rounded-lg p-4 shadow-lg hover:shadow-xl transition-shadow;
}

/* Enhanced responsive navigation - PERFECTED */
@media (max-width: 1024px) {
  .nav-desktop {
    @apply hidden;
  }
  
  .nav-mobile {
    @apply block;
  }
}

@media (min-width: 1025px) {
  .nav-desktop {
    @apply block;
  }
  
  .nav-mobile {
    @apply hidden;
  }
}

/* Image upload component styles - PERFECTED */
.image-upload-area {
  @apply border-2 border-dashed border-gray-300 dark:border-gray-600 rounded-lg p-6 text-center hover:border-red-400 hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-all duration-200 cursor-pointer;
}

.image-upload-area.drag-active {
  @apply border-red-500 bg-red-50 dark:bg-red-900/20;
}

.image-preview {
  @apply relative rounded-lg overflow-hidden;
}

.image-preview:hover .image-preview-overlay {
  @apply opacity-100;
}

.image-preview-overlay {
  @apply absolute inset-0 bg-black bg-opacity-50 opacity-0 transition-opacity flex items-center justify-center space-x-2;
}

/* News and content styles - PERFECTED */
.news-card {
  @apply card hover:shadow-lg transition-all duration-200 hover:scale-105 cursor-pointer;
}

.news-card:hover .news-image {
  @apply scale-105;
}

.news-image {
  @apply transition-transform duration-300;
}

.news-category-badge {
  @apply px-2 py-1 text-xs font-bold rounded-full;
}

/* Form improvements - PERFECTED */
.form-section {
  @apply card p-6 space-y-6;
}

.form-section h3 {
  @apply text-lg font-semibold text-gray-900 dark:text-white border-b border-gray-200 dark:border-gray-700 pb-2 mb-4;
}

.form-row {
  @apply grid grid-cols-1 md:grid-cols-2 gap-6;
}

.form-row-full {
  @apply md:col-span-2;
}

/* Voting system styles - PERFECTED */
.vote-buttons {
  @apply flex flex-col items-center space-y-1;
}

.vote-button {
  @apply p-1 rounded hover:bg-gray-100 dark:hover:bg-gray-700 text-gray-500 transition-all duration-200;
}

.vote-button:hover {
  @apply scale-110;
}

.vote-button.upvote:hover {
  @apply text-green-600;
}

.vote-button.downvote:hover {
  @apply text-red-600;
}

.vote-score {
  @apply text-sm font-medium text-gray-700 dark:text-gray-300;
}

/* Scrollbar styling - PERFECTED */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100 dark:bg-gray-800;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-400 dark:bg-gray-600 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gray-500 dark:bg-gray-500;
}

/* Print styles - PERFECTED */
@media print {
  .no-print {
    display: none !important;
  }
  
  .card {
    @apply border border-gray-300 shadow-none;
  }
  
  body {
    @apply bg-white text-black;
  }
}

/* Error and success states - PERFECTED */
.error-state {
  @apply bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 text-red-800 dark:text-red-200 p-4 rounded-lg;
}

.success-state {
  @apply bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 text-green-800 dark:text-green-200 p-4 rounded-lg;
}

.warning-state {
  @apply bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 text-yellow-800 dark:text-yellow-200 p-4 rounded-lg;
}

/* Loading states - PERFECTED */
.skeleton {
  @apply bg-gray-200 dark:bg-gray-700 animate-pulse rounded;
}

.skeleton-text {
  @apply skeleton h-4 mb-2;
}

.skeleton-title {
  @apply skeleton h-6 mb-4;
}

.skeleton-avatar {
  @apply skeleton w-10 h-10 rounded-full;
}

/* Final polish - smooth transitions everywhere */
* {
  transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease, box-shadow 0.2s ease;
}

/* Mobile-specific dark mode consistency */
@media (max-width: 980px) {
  /* Ensure proper dark mode colors on mobile */
  .dark .card {
    @apply bg-gray-800 border-gray-700;
  }
  
  .dark .mobile-nav-content {
    @apply bg-gray-800 border-gray-700;
  }
  
  .dark input, 
  .dark select, 
  .dark textarea {
    @apply bg-gray-700 border-gray-600 text-white;
  }
  
  /* Mobile-specific performance optimizations */
  * {
    -webkit-tap-highlight-color: transparent;
  }
  
  /* Disable hover effects on touch devices */
  @media (hover: none) {
    .hover\:bg-gray-50:hover,
    .hover\:bg-gray-100:hover {
      background-color: transparent !important;
    }
  }
}

/* Ensure perfect spacing in all components */
.space-y-1 > :not([hidden]) ~ :not([hidden]) {
  margin-top: 0.25rem;
}

.space-y-2 > :not([hidden]) ~ :not([hidden]) {
  margin-top: 0.5rem;
}

.space-y-3 > :not([hidden]) ~ :not([hidden]) {
  margin-top: 0.75rem;
}

.space-y-4 > :not([hidden]) ~ :not([hidden]) {
  margin-top: 1rem;
}

.space-y-6 > :not([hidden]) ~ :not([hidden]) {
  margin-top: 1.5rem;
}

/* Perfect VLR.gg match scores */
.match-score {
  @apply font-bold text-lg px-2 py-1 rounded;
}

.match-score.live {
  @apply text-red-600 dark:text-red-400 bg-red-50 dark:bg-red-900/20;
}

.match-score.completed {
  @apply text-green-600 dark:text-green-400;
}

/* Perfect team shortnames */
.team-short {
  @apply font-mono font-bold tracking-wide;
}

/* Responsive container padding utility */
.container-padding {
  @apply px-4 sm:px-6 lg:px-8;
}

/* Responsive text utilities */
.text-responsive-xs {
  @apply text-xs sm:text-sm;
}

.text-responsive-sm {
  @apply text-sm sm:text-base;
}

.text-responsive-base {
  @apply text-base sm:text-lg;
}

.text-responsive-lg {
  @apply text-lg sm:text-xl;
}

/* Responsive spacing utilities */
.space-responsive {
  @apply space-y-3 sm:space-y-4;
}

.gap-responsive {
  @apply gap-3 sm:gap-4;
}

/* Mobile-first card padding */
.card-padding {
  @apply p-3 sm:p-4 md:p-6;
}

/* Mobile-specific enhancements - VLR.gg style */
@media (max-width: 980px) {
  /* Remove body scroll when mobile menu open */
  body.mobile-menu-open {
    overflow: hidden;
  }
  
  /* Mobile navigation drawer */
  .mobile-nav-drawer {
    @apply fixed inset-0 bg-black bg-opacity-50 z-50;
  }
  
  .mobile-nav-content {
    @apply fixed right-0 top-0 h-full w-64 bg-white dark:bg-gray-800 shadow-xl transform transition-transform duration-300;
  }
  
  /* Mobile match cards - VLR.gg style */
  .match-card-mobile {
    @apply p-3 space-y-2 border-l-4 border-transparent hover:border-red-500 transition-all;
  }
  
  /* Mobile forum threads */
  .forum-thread-mobile {
    @apply block space-y-1 p-3 border-b border-gray-200 dark:border-gray-700;
  }
  
  .forum-thread-mobile .thread-title {
    @apply text-sm font-medium line-clamp-2;
  }
  
  .forum-thread-mobile .thread-meta {
    @apply text-xs text-gray-500 flex items-center gap-2;
  }
  
  /* Mobile tables */
  .mobile-table {
    @apply block;
  }
  
  .mobile-table thead {
    @apply hidden;
  }
  
  .mobile-table tbody {
    @apply block;
  }
  
  .mobile-table tr {
    @apply block mb-3 p-3 border border-gray-200 dark:border-gray-700 rounded;
  }
  
  .mobile-table td {
    @apply block text-right;
  }
  
  .mobile-table td:before {
    content: attr(data-label);
    @apply float-left font-medium text-gray-500 dark:text-gray-400;
  }
  
  /* Mobile brackets */
  .bracket-mobile {
    @apply overflow-x-auto pb-4;
  }
  
  .bracket-mobile-scroll {
    @apply min-w-[600px];
  }
  
  /* Touch-friendly buttons */
  .touch-target {
    @apply min-h-[44px] min-w-[44px] flex items-center justify-center;
  }
  
  /* Mobile-optimized images */
  .image-mobile {
    @apply w-full h-auto max-h-48 object-cover;
  }
  
  /* Mobile stats cards */
  .stat-card-mobile {
    @apply grid grid-cols-2 gap-2 text-center;
  }
  
  .stat-value-mobile {
    @apply text-lg font-bold;
  }
  
  .stat-label-mobile {
    @apply text-xs text-gray-500;
  }
}

/* Small device optimizations */
@media (max-width: 375px) {
  /* Ultra-compact navigation */
  .nav-logo {
    @apply text-base;
  }
  
  /* Smaller buttons */
  .btn-mobile-sm {
    @apply px-2 py-1 text-xs;
  }
  
  /* Compact match cards */
  .match-team-logo {
    @apply w-6 h-6;
  }
  
  .match-team-name {
    @apply text-xs max-w-[100px];
  }
  
  /* Minimal padding */
  .container-mobile-sm {
    @apply px-2;
  }
}

/* VLR.gg-style responsive utilities */
.mobile-only {
  display: block;
}

.tablet-only {
  display: none;
}

.desktop-only {
  display: none;
}

@media (min-width: 641px) {
  .mobile-only {
    display: none;
  }
  
  .tablet-only {
    display: block;
  }
}

@media (min-width: 980px) {
  .tablet-only {
    display: none;
  }
  
  .desktop-only {
    display: block;
  }
}

/* Mobile-first layout adjustments */
.mobile-content {
  padding-top: 56px; /* Header height */
  padding-bottom: 64px; /* Bottom nav height */
}

@media (min-width: 980px) {
  .mobile-content {
    padding-top: 0;
    padding-bottom: 0;
  }
}

/* Touch-optimized interactive elements */
@media (max-width: 979px) {
  /* Increase tap targets */
  a, button, [role="button"], input, select, textarea {
    min-height: 44px;
  }
  
  /* Better spacing for touch */
  .touch-list > * + * {
    margin-top: 8px;
  }
  
  /* Swipeable containers */
  .swipe-container {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory;
  }
  
  .swipe-item {
    scroll-snap-align: start;
    flex-shrink: 0;
  }
}

/* Tablet-specific optimizations */
@media (min-width: 641px) and (max-width: 979px) {
  /* Two-column layouts */
  .tablet-grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }
  
  /* Optimized card sizes */
  .tablet-card {
    min-height: 120px;
  }
  
  /* Better use of horizontal space */
  .tablet-flex-row {
    flex-direction: row;
    align-items: center;
  }
}